.row-poster {
    width: 100%;
    object-fit: contain;
    /* keeps the ratio */
    max-height: 150px;
    transition: transform 350ms;
    margin: 10px;
}



.row-poster:hover {
    transform: scale(1.10);
}

.row-posters {
    display: flex;
    /* this helps us to scroll through only the row while keeping everything else fixed.  */
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 25px;
}

/* now adding this will remove the scroll bar that we get from overflow-x */
.row-posters::-webkit-scrollbar {
    display: none;
} 

.row-poster-large {
    max-height: 350px;
}

.row {
    margin-left: 20px;
    color: #fff;
}